import prisma from '@/lib/prisma';
import { NextResponse } from 'next/server';

export async function GET() {
    try {
        const categories = await prisma.polaRuangRevisiRtrw.findMany({
            distinct: ['namobj'],
            select: {
                namobj: true,
            },
            where: {
                namobj: {
                    not: null,
                },
            },
        });

        // Extract just the strings and filter any potential nulls/empties
        const categoryNames = categories
            .map((c) => c.namobj)
            .filter((name): name is string => !!name);

        return NextResponse.json(categoryNames);
    } catch (error) {
        console.error('Error fetching legend categories:', error);
        return NextResponse.json(
            { error: 'Internal Server Error' },
            { status: 500 }
        );
    }
}
